.btn{
  margin-left: 5px;
  background-color: blue;
  color: white;
  font-size: 15px;
  font-weight: 100px;
}
.btn:hover{
  background-color: #000000;
  color: white;
  font-size: 15px;
  font-weight: 100px;
}
.form_text{
  text-align: center;
  display: flex;
  margin-bottom: 20px;
}
.btn_style{
  margin-bottom: 10px;
  text-align: center;
  background-color: blue;
  color: white;
  border: 3px solid blue;
  border-radius: 5px;
  padding: 3px 4px;
  font-size: 15px;
  font-weight: 100px;
}
.btn_style:hover{
  background-color: black;
  color: white;
  border: 3px solid black;
  border-radius: 5px;
  padding: 3px 4px;
  font-size: 15px;
  font-weight: 100px;
}
.remove_list{
  font-size: 20px;
  font-weight: 500;
}

.front_page{
  box-shadow: 0 0 40px 5px rgb(0 0 0 / 5%);
  width: 100%;
  height: 100%;
}
h1{
  text-align: center;
  margin-bottom: 10px;
}